
import { defineComponent } from 'vue'
import ViewPort from '../DataViews/ViewPort.vue'
import { Option } from '@/components/Forms/FieldInterface'
import FieldMixinVue from './FieldMixin.vue'
import {
    IonGrid,
    IonCol,
    IonRow,
    IonInput
} from "@ionic/vue"
export default defineComponent({
  components: { 
    ViewPort,
    IonGrid,
    IonCol,
    IonRow,
    IonInput
  },
  mixins: [FieldMixinVue],
  data: () => ({
    columns: [] as any[],
    listData: [] as Option[]
  }),
  watch: {
    listData: {
        handler(listData) {
            const allEmpty = listData.every((d: Option) => {
                return d.other.fields.every((x: Option) => x.value === '')
            })
            if (allEmpty) {
                return this.$emit('onValue', null)
            } else {
                this.$emit('onValue', this.listData)
            }
        },
        deep: true
    }
  },
  mounted() {
    this.init()
  },
  activated() {
    this.init()
  },
  methods: {
    clearValue() {
        this.listData.forEach((item: Option) => {
            Array.isArray(item.other.fields) && 
                item.other.fields.forEach((i: Option) => {
                    if (typeof i.clearable === 'boolean' && i.clearable || i.clearable === undefined) {
                        i.value = ''
                        i.disabled = false
                    }
                })
        })
    },
    async init() {
        this.$emit('onFieldActivated', this)
        if (Array.isArray(this.config.columns)) this.columns = this.config.columns
        this.listData = await this.options(this.fdata, this.cdata, this.listData)
    }
  }
})
